:root {
  --main-bg: #ffffff;
  --main-text:  #1b2733;
  --alt-text:  #e0e0e0;
  --gradient1: rgba(38, 38, 114, 0.3);
  --gradient2: rgba(56, 56, 141, 0);
  --gradient3: rgba(56, 100, 141, 0.3);
  --gradient4: rgba(56, 100, 141, 0);
  --main-dark: rgba(38, 38, 114, 0.8);

  --alt-bg: #637282;
  --h1-color: var(--alt-text);
  --header-bg: var(--main-dark);
  --alt-bg-opaque: rgba(38, 38, 114, 0.5);

  --header-text: #ffffff;
  --button: var(--main-dark);
  --eligible: #00ff99;
  --error: rgba(238, 11, 11, 0.651);
}

/* 🎨 colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

main {
  display: grid;
  height: 100vh;
  overflow-y: scroll;
  max-height: 95%;
  background: #ffffff;
  background: var(--main-bg);
  background:          
            linear-gradient(336deg, rgba(38, 38, 114, 0.3), rgba(56, 56, 141, 0) 70.71%),
            linear-gradient(127deg, rgba(56, 100, 141, 0.3), rgba(56, 100, 141, 0) 70.71%);
  background:          
            linear-gradient(336deg, var(--gradient1), var(--gradient2) 70.71%),
            linear-gradient(127deg, var(--gradient3), var(--gradient4) 70.71%);         
  grid-template-columns: minmax(100%, 10%);
  text-align: center;
  font-family: 'Karla', 'Rubik', sans-serif;
  /* position: relative; */
  min-width: 300px;
  margin: 0 auto;
  padding: 0;

  /* font-family: 'Rubik', sans-serif; */
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1 {
  margin: 50px auto;
  padding: 5px;
  background: rgba(38, 38, 114, 0.5);
  background: var(--alt-bg-opaque);
  max-width: 100%;
  color: #e0e0e0;
  color: var(--h1-color);
  font-size: 2rem;
  border-radius: 8px;
}

h2 {
  /* width: 100vw; */
  background-color: rgba(38, 38, 114, 0.8);
  background-color: var(--header-bg);
  font-size: 1.7rem;
  color: #ffffff;
  color: var(--header-text);
  margin: 25px 0 0;
  padding: 25px 5px;
}

h2.round-robin {
  margin-top: 0;
}

h3 {
  color: #637282;
  color: var(--alt-bg);
  font-size: 1.6rem;
  margin: 30px 0 0;
  padding: 5px;
  width: auto;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.footer {
  background-color: rgba(38, 38, 114, 0.5);
  background-color: var(--alt-bg-opaque);
  height: 70px;
  display: flex;
  flex-direction: column-reverse;
  padding: 10px;
  font-size: 0.7rem;
  color: white;
}

.display-none {
  display: none;
}

.greeting {
  font-size: 1.4rem;
  margin: 0 10px 30px;
  font-weight: bold;
}

.tabWrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.tab {
  cursor: pointer;
  font-size: 1.4rem;
  padding: 10px;
  border-radius: 4px;
  font-weight: bold;
  letter-spacing: 1.5px;
}

.react-tabs__tab-panel {
  min-height: 100vh;
}

.small-tab {
  font-size: 0.9rem;
}

.instructions {
  background-color: rgba(38, 38, 114, 0.5);
  background-color: var(--alt-bg-opaque);
  color: white;
  font-size: 1.2rem;
  padding: 5px;
  margin: 0;
}

.scoring {
  font-weight: 800;
  font-size: 1.35rem;
  color: #e0e0e0;
  color: var(--alt-text);
}

.num {
  color: #00ff99;
  color: var(--eligible);
  font-weight: bold;
}

.name-input {
  display: flex;
  margin-top: 40px;
  width: 200px;
  padding: 0;
}

.name-input .null {
  display: none;
}

.name-input > input {
  border: 2px solid transparent;
  height: 40px;
  margin-left: calc(50vw - 100px);  
  border-radius: 20px;
  width: 200px;
  text-align: center;
  font-size: 1.4rem;
}

.name-input:hover input {
  border: 2px solid #637282 !important;
  border: 2px solid var(--alt-bg) !important;
}

.name-input > input:focus {
  outline: none;
  border: 2px solid rgba(38, 38, 114, 0.5) !important;
  border: 2px solid var(--alt-bg-opaque) !important;
  background-color: #ffffff;
  background-color: var(--main-bg);
  border: none;
}

button {
  background-color: rgba(38, 38, 114, 0.8);
  background-color: var(--button);
  cursor: pointer;
  color: white;
  min-width: 100px;
  max-width: 300px;
  height: 40px;
  margin: 30px auto 120px;
  border-radius: 4px;
  font-size: 1.3rem;
}

.status {
  margin: 12px;
}

.status.saved, .status.not_saved {
  margin: -80px auto 12px;
  background-color: #00ff99;
  background-color: var(--eligible);
  padding: 20px;
  font-size: 1.2rem;
  font-weight: 750;
  color: #1b2733;
  color: var(--main-text);
  width: 80vw;
  border-radius: 4px;
}

.status.status.not_saved {
  background-color: rgba(238, 11, 11, 0.651);
  background-color: var(--error);
  color: #e0e0e0;
  color: var(--alt-text);
}

button:disabled {
  background-color: grey;
  cursor: not-allowed;
}

.logo {
  height: 70px;
  pointer-events: none;
  position: absolute;
  left: 10px;
  top: 10px;
}

.left-aside, .right-aside {
  background:  
    linear-gradient(336deg, rgba(38, 38, 114, 0.8), rgba(56, 56, 141, 0) 70.71%),
    linear-gradient(127deg, rgba(56, 56, 141, 0.8), rgba(38, 38, 114, 0) 70.71%);
  position: absolute;
  z-index: 10;
  top: 0;
  height: 100%;
  width: 90px;
  min-height: 100vh;
  max-height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.left-aside {
  left: 0;
}

.right-aside {
  right: 0;
}

.results {
  display: grid;
}

@media screen and (max-width: 1050px) { 
  aside {
      display: none !important;
  }
}


@-webkit-keyframes nhl-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  45% {
    transform: rotate(5deg);
  }
  46% {
    transform: rotate(175deg);
  }
  54% {
    transform: rotate(185deg);
  }
  55% {
    transform: rotate(355deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


@keyframes nhl-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  45% {
    transform: rotate(5deg);
  }
  46% {
    transform: rotate(175deg);
  }
  54% {
    transform: rotate(185deg);
  }
  55% {
    transform: rotate(355deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

